.contactForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    width: 400px;
    margin: 0 auto;
    border: 1px solid #007bb5; /* Темно-синий цвет */
    border-radius: 4px;
    padding: 25px;
    background-color: #e0f7fa; /* Светло-синий фон */
  }
  
  .inputWrapper {
    width: 100%;
  }
  
  .formInput {
    margin-top: 10px;
    width: 100%;
    padding: 10px;
    border: 1px solid #007bb5; /* Темно-синий цвет */
    border-radius: 25px;
  }
  
  .formInput:focus {
    outline-color: #007bb5; /* Темно-синий цвет */
  }
  
  .formButton {
    padding: 10px 30px;
    border: 1px solid #007bb5; /* Темно-синий цвет */
    border-radius: 25px;
    font-size: 14px;
    background-color: #e0f7fa; /* Светло-синий фон */
    color: #007bb5; /* Темно-синий текст */
    /* transition: transform 0.3s ease-in-out; */
  }
  
  .formButton:hover {
    cursor: pointer;
    transform: scale(1.1);
    background-color: #007bb5; /* Темно-синий цвет */
    color: white;
    font-weight: 500;
  }
  